.main-allmembers-section {
    background-color: #150f22;
    padding-bottom: 120px;
}

.main-gradient {
    background-image: url("../../../../../assets/images/internalpages-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
}

.heading-div {
    padding: 93px 0px;

    h1 {
        color: #fff;
        font-family: Urbanist;
        font-size: 48px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }

    p {
        color: #fff;
        font-family: Urbanist;
        font-style: normal;
        line-height: normal;
    }
}

.bottom-margin {
    margin-bottom: 100px;
}

.read-more-heading {
    font-size: 24px;
    font-weight: 600;
}

.news-video {
    max-width: 553px;
    max-height: 340px;
    width: auto;
    height: auto;
    border-radius: 16px;
}

.truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    width: 100%;
}


.common-text-style {
    font-style: normal;
    line-height: normal;
    text-transform: capitalize;
    font-family: 'Urbanist';
    color: #FFFFFF;
}


.news-link {
    overflow: hidden;
    color: #bbb !important;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: 'Urbanist';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: lowercase;
}

/* Published date */
.published-date {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 16px;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #fff !important;
}

.news-link:hover {
    text-decoration: underline;
    color: rgb(76, 76, 248) !important;
}

.blog-img {
  width: 100%;
  height: auto;
  max-width: 819px;
  max-height: 392px;
}

@media (min-width: 768px) {
  .blog-img {
    min-width: 600px;
  }
}

.font-size{
    font-size: 22px;
    font-weight: 600;
}

.heading-content{
    font-size: 40px;
    font-weight: 600;
}

.main-text-content{
    font-size: 16px;
    font-weight: 400;
    color: #fff !important;
}

.latest-news-line {
  height: 4px;
  max-width: 222px;
  flex-grow: 1; 
  background: linear-gradient(130deg, #492FB2 -1.74%, rgba(73, 47, 178, 0.61) 125.81%);
  border-radius: 4px;
}


@media (max-width: 576px) {
  .latest-news-line {
    max-width: 120px;
  }
}

.latest-divide-line {
  height: 1px;
  max-width: 373px;
  flex-grow: 1; 
  background: linear-gradient(0deg, rgba(101, 130, 255, 0.18) 0%, rgba(192, 91, 218, 0.18) 100%);
  border-radius: 4px;
}

